<template>
	<div>
		<keep-alive>
			<Layout>
				<Sider hide-trigger>
					<Menu active-name="0" :open-names="['4']" style="width:auto" ref="menu">
						<Submenu name="4">
							<template slot="title">
								<Icon type="md-body" />ChatList
							</template>
							<MenuItem v-for="(persion,index) in chatList" :name="index.toString()" :key="persion.name" @click.native="change(index)">{{persion.name}}</MenuItem>
						</Submenu>
					</Menu>
				</Sider>
				<Layout>
					<Content>
						<div class="content">
							<div class="chat-container">
								<Chat v-if="visible" :participants="current" :myself="myself" :messages="currentMessage" :onType="onType"
								 :onMessageSubmit="onMessageSubmit" :chatTitle="chatTitle" :placeholder="placeholder" :colors="colors"
								 :borderStyle="borderStyle" :onClose="onClose" :hideCloseButton="hideCloseButton" :closeButtonIconSize="closeButtonIconSize"
								 :submitIconSize="submitIconSize" :asyncMode="asyncMode">
									<template v-slot:header>
										<div class="header-slot">
											<a v-for="(participant, index) in current" :key="index" class="custom-title">{{`${participant.name} `}}</a>
										</div>
									</template>
								</Chat>
							</div>
						</div>
					</Content>
				</Layout>
			</Layout>
		</keep-alive>
	</div>
</template>
<script>
	import eventBus from '../../eventBus/friendsBus'
	import {
		Chat
	} from "vue-quick-chat";
	import moment from "moment";
	import SockJS from "sockjs-client";
	import Stomp from "stompjs";
	export default {
		name: "app",
		components: {
			Chat
		},
		created() {
			console.log("...................................Chat Page is created ...................................");
		},
		mounted: function() {
			console.log("...................................Chat Page is mounted ...................................");
			this.fun1()
			this.initWebSocket(); //建立连接
			this.change(0); //选中列表中第0个
		},
		beforeDestroy: function() {
			// 页面离开时断开连接,清除定时器
			console.log("beforeDestroy");
			this.disconnect();
			clearInterval(this.timer);
			//销毁Bus
			//eventBus.$destroy()
			console.log('...................................Chat Page is beforeDestroyed ...................................')
		},
		data() {
			return {
				chatList: [{
					name: "zzj",
					id: 0
				}, {
					name: "skr",
					id: 1
				}], //聊天列表id为索引号
				myself: {
					name: "myself",
					id: 1
				},
				current: [{
					name: "",
					id: 10
				}], //当前选中的聊天对象
				currentMessage: [], //与当前选中的聊天对象的聊天信息
				messageList: [ //总聊天信息，和chatList相对应
					[{
						content: "一号消息",
						myself: true,
						participantId: 1,
						timestamp: {
							year: 2012,
							month: 3,
							day: 5,
							hour: 20,
							minute: 10,
							second: 3,
							millisecond: 123
						},
						uploaded: true,
						viewed: true
					}, ],
					[{
						content: "二号消息",
						myself: true,
						participantId: 1,
						timestamp: {
							year: 2012,
							month: 3,
							day: 5,
							hour: 20,
							minute: 10,
							second: 3,
							millisecond: 123
						},
						uploaded: true,
						viewed: true
					}, ]
				],
				visible: true, //与v-if 一起使用，暂未用到
				chatTitle: "My chat title", //聊天标题 暂未用到（可改为聊天对象姓名）
				placeholder: "send your message", //输入框填充字
				colors: { //框头，信息，消息框的颜色设置
					header: {
						bg: "#708090",
						text: "#fff"
					},
					message: {
						myself: {
							bg: "#fff",
							text: "#bdb8b8"
						},
						others: {
							bg: "#fb4141",
							text: "#fff"
						},
						messagesDisplay: {
							//bg: 'rgb(247, 243, 243)',
							//bg: '#f7f3f3'
							bg: "#f7f3f3"
						}
					},
					submitIcon: "#b91010" //发送图标
				},
				borderStyle: { //聊天框border 曲角程度
					topLeft: "10px",
					topRight: "10px",
					bottomLeft: "10px",
					bottomRight: "10px"
				},
				hideCloseButton: false,
				submitIconSize: "40px",
				closeButtonIconSize: "20px",
				asyncMode: true
			};
		},
		methods: {
			change(index) { //切换聊天对象
				this.current[0].name = this.chatList[index].name;
				this.current[0].id = this.chatList[index].id;
				this.currentMessage = this.messageList[index];
			},
			onType: function(e) { //输入时的回调函数，暂未使用
				console.log("typing");
			},
			onMessageSubmit(message) { //点击发送的回调函数
				console.log(this.currentMessage);
				this.currentMessage.push(message);
				this.stompClient.send( //发送到后端
					"/app/chat", {},
					JSON.stringify({
						'to': this.current[0].name, // 发给谁
						'content': message.content, // 内容是啥
						'name': this.myself.name // 谁发的
					})
				);
				// timeout simulating the request
				setTimeout(() => { //发送延迟，2秒后显示发送成功
					message.uploaded = true;
				}, 2000);
			},
			onClose() { //关闭按钮的回调函数暂未使用
				this.visible = false;
			},
			connect() { //socket连接函数
				this.socket = new SockJS("/chat");
				this.stompClient = Stomp.over(this.socket);
				this.stompClient.connect({}, (frame) => {
					//serConnected(true);
					// 第一个参数是地址，不要改
					// 第二个参数是回调函数，在有新消息传过来之后会自动被调用
					// 输入的参数是消息
					this.stompClient.subscribe(
						"/user/queue/chat",
						(greeting) => {
							this.showGreeting(JSON.parse(greeting.body));
						},
						err => {
							// 连接发生错误时的处理函数
							console.log(err);
						}
					);
				});
			},
			initWebSocket() { //初始化连接接口
				this.connect();
			},
			removeTab(targetName) { //删除标签回调函数（暂未使用）
				console.log(targetName);
			},
			showGreeting(message) //接受后端的信息并显示,聊天对象未加入chatList，新建对象插入chatList
			{
				console.log("showGreeting");
				for (var i = 0, len = this.chatList.length; i < len; i++) {
					if (i.name == message.name) {
						this.messageList[i].push({
							content: message.content,
							myself: false,
							participantId: i,
							timestamp: {
								year: 2014,
								month: 3,
								day: 5,
								hour: 20,
								minute: 10,
								second: 3,
								millisecond: 123
							},
							uploaded: true,
							viewed: true
						});
						break;
					}
					if (i == len - 1) {
						this.chatList.push({
							name: message.name,
							id: i,
						});
						this.messageList.push([{
							content: message.content,
							myself: false,
							participantId: i,
							timestamp: {
								year: 2014,
								month: 3,
								day: 5,
								hour: 20,
								minute: 10,
								second: 3,
								millisecond: 123
							},
							uploaded: true,
							viewed: true
						}]);
					}
				}
			},
			disconnect() { //断开连接
				if (this.stompClient !== null) {
					this.stompClient.disconnect();
				}
			},
			fun1(){
				console.log('******fun1******')
				var that = this
				
				eventBus.$on('wantToChat', function(val){
					//console.log('........................................'+val)
					var id = that.chatList.length
					that.chatList.push({
						name: val,
						id: id,
					})
					//console.log(this.$uid)
					console.log(that.chatList)
					
				})
				
			}
		}
	};
</script>

<style>
	#app {
		font-family: "Avenir", Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		margin-top: 60px;
	}

	.content {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-evenly;
		flex-wrap: wrap;
	}

	.chat-container {
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgb(247, 243, 243);
		padding: 10px 0 10px 0;
		height: 700px;
		width: 800px;
	}

	.external-controller {
		background: #2c3e50;
		height: 300px;
		width: 600px;
		display: flex;
		color: #eee;
	}

	.controller-btn-container {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 20px;
		padding-right: 20px;
		flex-wrap: wrap;
	}

	.btn-message {
		cursor: pointer;
		background: #eee;
		border: none;
		height: 40px;
		color: #2c3e50;
		border-radius: 5px;
		outline: none;
		transition: 0.3s;
	}

	.btn-participant {
		cursor: pointer;
		background: #eee;
		border: none;
		height: 40px;
		color: #2c3e50;
		border-radius: 5px;
		outline: none;
		transition: 0.3s;
	}

	.btn-message:hover {
		background: rgb(255, 255, 255);
	}

	.custom-title {
		color: #eee;
	}

	.header-slot {
		max-height: 100%;
	}
</style>
